.Quote-Text-Value {
    font-family: Arial, serif;
}

.Quote-Container {
    width: 90%;
    margin: auto;
    background-color: unset;
}

.Quote-Header-Container {
    margin-top: 3rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

@media only screen and (max-width: 600px) {
    .Quote-Header-Container {
        flex-direction: column;
    }
}

.Quote-Header-Logo {
    flex-basis: 80%;
    text-align: left
}

.Quote-Spec-Container {
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.Quote-For-Container {
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 2rem;
}

.Quote-For-Inner-Container {
    display: flex;
    flex-direction: row;
    flex-basis: 40%;
}

.Quote-Info-Container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 1rem;
}

.Quote-Underline {
    text-decoration: underline;
    padding-bottom: 1.2rem;
}

.Quote-Label {
    font-size: 20px;
}

.Quote-Text {
    text-align: left;
}

.Quote-Text-Field {
    border-radius: 5px;
    background-color: rgba(195, 183, 79, 0.2);
    border: 1px solid black;
}

.Quote-Text-Area-Label {
    vertical-align: center;
    white-space: nowrap;
    padding-right: 0.5rem;
    text-align: left;
}

.Quote-Text-Field--Area {
    resize: vertical;
    width: 50%;
}
.Quote-Separator {
    margin-top: 2rem;
}

.Quote-Separator--Bottom {
    margin-bottom: 2rem;
}

.Quote-Text-Field--Area--extra {
    resize: none;
    width: 99%;
}

.Quote-Grid-Text-Area {
    resize: none;
    width: 98%;

}

.Quote-Grid-Number-Field {
    resize: none;
    width: 80%;
}

.Quote-Grid-Top-Aligned {
    vertical-align: top;
}


.Quote-Grid-Add-Button {
    width: 40%;
    border-radius: 7px;
    color: white;
    background-color:  rgba(195, 183, 79, 1);;
    font-size: 18px;
    border: none;
    padding: 0.5rem;
    margin-top: 0.5rem;
    max-height: 40px;
}

.Quote-Grid-Add-Button:hover {
    cursor: pointer;
    background-color: #7d3d15;
}

@media only screen and (max-width: 600px) {
    .Quote-Grid-Add-Button {
        font-size: 14px;
    }
}

.Quote-Grid-Delete-Button {
    border: none;
    background-color: unset;
    overflow: clip;

    position: absolute;
    left: -20px;
    top: 12px;
}

.Quote-Grid-Delete-Container {
    position: relative;
}

.Quote-Grid-Delete-Button:hover {
    cursor: pointer;
    color:  rgba(195, 183, 79, 1);
}

.Quote-Grid-Polyfill {
    width: 102%;
    text-align: left;
}

.Quote-Generate-Button {
    padding: 5rem 2rem;
}

.Quote-Grid-Static {
    display: flex;
    justify-content: flex-end;

    margin-top: 1rem;
    flex-basis: 90%;

    page-break-inside: avoid;
}

.Quote-Grid-Static-Headers {
    padding-right: 1rem;
}

.Quote-Grid-Footer-Layout {
    display: flex;
    justify-content: space-between;
}

.Quote-Grid-Shadow-Cell {
    width: 20px;
}

.Grid-Bar-Hidden {
    display: none;
}

.Quote-Transparent-Input {
    outline: 0;
    border: 0;
    font-family: Arial, serif;
    font-size: 20px;
    font-weight: bold;
    text-decoration: underline;
    padding: 0;

    width: 100%;
    text-align: center;
}